"use client"

import { Card } from "@/components/ui/card"
import { points } from "@/lib/product-list"
import { Star } from "lucide-react"
import Image from "next/image"
import Link from "next/link"

export default function Store() {
  return (
    <div className="container mx-auto px-4 py-8 space-y-8 min-h-screen">
      <div className="text-center space-y-4 opacity-0 animate-fade-in">
        <h1 className="text-4xl sm:text-5xl font-bold mb-4">
          <span className="font-bold text-yellow-400 mb-2">Toko Premium Points</span>
        </h1>
        <p className="text-lg text-gray-300">Beli Premium Points untuk membuka fitur eksklusif</p>
      </div>

      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
        {points.map((point) => (
          <Link
            key={point.id}
            href={`/store/product/${point.id}`}
            className="block opacity-0 animate-fade-in"
            style={{ animationDelay: point.delay }}
          >
            <Card className="group relative flex flex-col items-center justify-center gap-8 border-2 border-slate-700/50 bg-slate-900 px-6 py-12 transition-all duration-300 hover:border-yellow-400 hover:shadow-lg hover:shadow-yellow-400/20">
                {/* Premium Points Badge */}
                <div className="flex items-center gap-3 rounded-lg bg-yellow-400 px-6 py-3 shadow-lg transition-all duration-300 group-hover:bg-yellow-300">
                    <Star className="h-7 w-7 fill-white text-white" />
                    <div className="flex flex-col">
                        <Image
                            src={point.image || "/placeholder.svg"}
                            alt={`${point.amount} Premium Points`}
                            fill
                            className="object-cover"
                        />
                    </div>
                </div>

                {/* Price Badge */}
                {/* <div className="w-full rounded-lg bg-yellow-400 py-3 text-center shadow-lg transition-all duration-300 group-hover:bg-yellow-300">
                    <p className="text-2xl font-bold text-slate-900">{point.amount}</p>
                </div> */}
            </Card>
          </Link>
        ))}
      </div>
    </div>
  )
}
